import { Directive, ElementRef, EventEmitter, HostListener, Input, Output } from '@angular/core';

@Directive({
  selector: '[appClickOutside]'
})

export class ClickOutsideDirective {

  @Input() appClickOutside: boolean = false;
  @Input() isActive: boolean = true;

  @Output() hideElement: EventEmitter<boolean> = new EventEmitter<boolean>();

  @HostListener('document:click', ['$event'])
  onClick($event) {
    if (this.appClickOutside) this.onHostClick($event);
  }

  @HostListener('document:touchstart', ['$event'])
  onTouch($event) {
    if (this.appClickOutside) this.onHostClick($event);
  }

  constructor(private element: ElementRef) {}

  private onHostClick(event: Event): void {
    if (!this.isActive) return;
    const isContains: boolean = this.element.nativeElement.contains(<Node>event.target);
    if (!isContains) {
      this.hideElement.emit(true);
    }
  }
}
