import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
})
export class AppComponent implements OnInit{

  public searchFieldTrigger = true;

  public menuList = [
    {
      title: 'Сеты',
      route: 'sushi'
    },
    {
      title: 'Роллы',
      route: 'rolls'
    },
    {
      title: 'WOK',
      route: 'wok'
    },
    {
      title: 'Супы',
      route: 'soup'
    },
    {
      title: 'Салаты',
      route: 'salad'
    },
    {
      title: 'Сашими',
      route: 'sashimi'
    },
    {
      title: 'Запеченные роллы',
      route: 'hot-rolls'
    },
    {
      title: 'Рисовая пицца',
      route: 'rice-pizza'
    },
    {
      title: 'Десерты',
      route: 'deserts'
    },
    {
      title: 'Добавки',
      route: 'adds'
    }
  ];

  public sidebarShrink: boolean = true;
  public outsideClicker: boolean = false;

  constructor(private route: Router) {}

  ngOnInit() {
    this.route.events.subscribe( () => {
      window.scrollTo(0, 0);
    });
  }

  public triggerSidebar() {
    if (this.outsideClicker) {
      this.sidebarShrink = true;
      this.outsideClicker = false;
    } else {
      this.sidebarShrink = false;
      setTimeout(() => {
        this.outsideClicker = true;
      }, 100);
    }
  }

  public hideSidebar() {
    setTimeout(() => {
      this.sidebarShrink = true;
      this.outsideClicker = false;
    }, 100);
  }

}
